/*--------------------------------------------------------------
# TodoItem
--------------------------------------------------------------*/

.item{
    background: var(--color8);
    background-attachment: fixed;
    font-size: 1rem;
    margin-right: 30px;
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 700px;
    color: var(--white-light);
}
.text-item{
    padding: 20px 0 20px 0;
    margin: 0 0 0 10px;
    font-weight: 700;
}

.text-item--completed{
    text-decoration: line-through;
}

.bx-container{
    display: flex;
    margin-right: 10px;
    margin-left: 10px;
    align-items: center;
}

.bx-container-delete{
    position:relative;
    right: -2.5rem;
}

.icon-svg{
    width: 30px;
    height: 30px;
}


.bx-task--active{
    color: var(--color6);
}

.bx-container-delete:hover .icon-svg{
    fill: var(--color3);
    cursor: pointer;
}
.bx-container-completed:hover .icon-svg{
    fill: var(--color6);
    cursor: pointer;
}

@media (min-width: 992px) {
    .item{
        margin-left: 50px;
    }

} 


@media (min-width: 768px) and (max-width: 991px) {
    .item{
        max-width: 900px;
    }
}
